@import url('../css/Var.css');

.container-carrusel-logo {
  width: 350px;
  height: 300px;
  border-radius: 20px 20px 0px 0px;
  background: linear-gradient(180deg, #171a1b 0%, rgba(23, 26, 27, 0) 100%);
  opacity: 0.8;
  
}

.container-carrusel-circle {
  width: 20%;
  height: 20px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-left: 5px;
  margin-top: 5px;
}

.circle-red {
  width: 10px;
  height: 10px;
  border-radius: 30px;
  background-color: var(--red);
}

.circle-yellow {
  width: 10px;
  height: 10px;
  border-radius: 30px;
  background-color: var(--yellow);
}

.circle-green {
  width: 10px;
  height: 10px;
  border-radius: 30px;
  background-color: var(--green);
}

.container-logo {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container-logo h3 {
  font-size: 2rem;
  position: relative;
  color: var(--white);
}

.container-logo h5 {
  font-size: 1.5rem;
  position: relative;
  color: var(--white);
  font-weight: 200;
}

.container-logo img {
  position: relative;
  top: 10px;
  width: 170px;
  height: 170px;
  transition: all 1.2s ease;
}


.container-logo img:hover {
  animation: shake 1s forwards;
}


@keyframes shake{
  10%{
    transform: rotate(15deg);
  }

  20%{
    transform: rotate(-15deg);
  }
  30%{
    transform: rotate(15deg);
  }
  50%{
    transform: rotate(0deg);
  }
}

@media screen and (min-width: 1329px) and (max-width: 1439px) {
  .container-carrusel-logo {
    width: 380px;
    
  }
  
  .container-logo img {
    width: 200px;
    height: 200px;
  }
  
}


@media screen and (min-width: 1024px) and (max-width: 1328px) {
  .container-carrusel-logo {
    width: 335px;
    
  }
  
  .container-logo img {
    width: 200px;
    height: 200px;
  }
  
}


@media screen and (min-width: 768px) and (max-width: 1023px){

  .container-logo {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .container-carrusel-logo {
    width: 280px;
    height: 260px;
  }

  .container-logo h3 {
    font-size: 1.7rem;
  }

  .container-logo img {
    position: relative;
    width: 150px;
    height: 150px;
    transition: all 1.2s ease;
  }
}

@media screen and (min-width: 481px) and (max-width: 767px){
  .container-carrusel-logo {
    width: 230px;
    height: 230px;
  }
  
  
  .container-logo h3 {
    font-size: 1.5rem;
  }
  
  .container-logo h5 {
    font-size: 1.2rem;
  }
  
  .container-logo img {
    width: 135px;
    height: 135px;
  }
  
}

@media screen and (min-width: 320px) and (max-width: 480px){
  .container-carrusel-logo {
    width: 250px;
    height: 250px;
  }
  
  
  .container-logo h3 {
    font-size: 1.5rem;
  }
  
  .container-logo h5 {
    font-size: 1.2rem;
  }
  
  .container-logo img {
    width: 125px;
    height: 125px;
  }
}