@import url("../css/Var.css");


.container-proyects{
    width: 1440px;
    margin: 0 auto;
    margin-bottom: 50px;
   height: auto;
   
}

.container-proyects-title{
    width: 90%;
    position: relative;
    left: 48px;
}

.container-proyects-title h1{
    width: 100%;
    font-size: 4rem;
    color: white;
    margin-bottom: 80px;
  
}

@media screen and (min-width: 1329px) and (max-width: 1439px) {
    .container-proyects{
        width: 100%;
    }
    
    .container-proyects-title{
        width: 96%;
        left: 20px;
    }
    
    .container-proyects-title h1{
        font-size: 3.5rem;
      
    }
}

@media screen and (min-width: 1024px) and (max-width: 1328px) {
    .container-proyects{
        width: 100%;
    }
    
    .container-proyects-title{
        width: 96%;
        left: 20px;
    }
    
    .container-proyects-title h1{
        font-size: 3.5rem;
      
    }
}


@media screen and (min-width: 768px) and (max-width: 1023px) {
    .container-proyects{
        width: 100%;   
    }
    
    .container-proyects-title{
        width: 96.5%;
        left: 10px;
    }
}




@media screen and (max-width: 468px) {
    .container-proyects{
        width: 468px;   
    }
    
    .container-proyects-title{
        width: 96%;
        position: relative;
        left: 10px;
    }
    
    .container-proyects-title h1{
        width: 100%;
        font-size: 3.5rem;
        color: white;
        margin-bottom: 80px;
      
    }
}


@media screen and (min-width: 481px) and (max-width: 767px){
    .container-proyects{
        width: 100%;   
        height: auto;
        position: relative;
        top: 90px;
    }

    .container-proyects-title {
        width: 96%;
        position: relative;
        left: 10px;
    }
    
}

@media screen and (min-width: 320px) and (max-width: 480px){
    .container-proyects{
        width: 100%;   
        height: auto;
        position: relative;
        top: 90px;
    }

    .container-proyects-title {
        width: 96%;
        position: relative;
        left: 5px;
    }
    
}