@import url("../css/Var.css");

.container-proyects-personal{
    height: 350px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 50px;
    width: 93%;
    margin-left: 48px;
    margin: 0 auto;
    
}

#proyects{
    margin-top: 50px;
}

.container-proyects-image img{
   width: 100%;
   height: 100%;
   border: 1px solid var(--gray);
    border-radius: 10px;

  
}

.container-proyects-description{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 40px;
    
}


.container-proyects-description h1{
    font-size: 2.5rem;
    color: var(--aqua);
}

.container-proyects-icon{
    display: flex;
    width: 150px;
   
   
}


.container-proyects-description p{
   font-size: 2rem;
   color: var(--white);
}

.container-proyects-link{
    width: 170px;
    display: flex;
    justify-content: space-between;

}

.container-proyects-link a{
    text-decoration: none;
    font-size: 1.5rem;
    color: var(--white);
    position: relative;
    font-weight: bold;
}

.container-proyects-link a::after {
    content: '';
    width: 0;
    height: 4px;
    background-color: var(--aqua);
    position: absolute;
    top: 25px;
    right: 50%;
    transition: all 0.6s;
    will-change: width;
    
  }

  .container-proyects-link a:hover:after {
    width: 100%;
    right: 0;
  }

  @media screen and (min-width: 1329px) and (max-width: 1439px) {
    .container-proyects-personal{
        width: 96.5%;
        margin-left: 20px;
        
    }
    
    .container-proyects-image img{
        object-fit: cover;
      
    }
     
  }

  @media screen and (min-width: 1024px) and (max-width: 1328px) {
    .container-proyects-personal{
        width: 96.5%;
        margin-left: 20px;
        
    }
    
    .container-proyects-image img{
        object-fit: cover;
      
    }
     
  }

  @media screen and (min-width: 768px) and (max-width: 1023px){
    .container-proyects-personal{
        height: 750px;
        display: flex;
        flex-direction: column;
        margin-top: 50px;
        width: 96.5%;
        margin-left: 10px;
        text-align: center;
    }

    .container-proyects-icon {
        display: flex;
        justify-content: center;
        gap: 16px;
        margin-left: 5px;
    }
        
    .container-proyects-description{
       align-items: center;
       text-align: center;
    }
    
  }




  @media screen and (min-width: 481px) and (max-width: 767px){
    .container-proyects-personal {
        width: 96%;
        height: auto;
        margin-left: 10px;
        display: flex;
        flex-direction: column;
    }

    .container-proyects-description {
        width: 100%;
        height: 400px;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        padding: 40px;
        align-items: center;
    }

    .container-proyects-icon { 
        justify-content: center;
        margin-left: 15px;
    }

    .container-proyects-description p {
       text-align: center;
      
    }
  }

  @media screen and (min-width: 320px) and (max-width: 480px){
    .container-proyects-personal {
        width: 96%;
        height: auto;
        margin-left: 5px;
        display: flex;
        flex-direction: column;
    }

    .container-proyects-description {
        width: 100%;
        height: 400px;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        padding: 40px;
        align-items: center;
    }

    .container-proyects-icon { 
        justify-content: center;
        margin-left: 15px;
    }

    .container-proyects-description p {
       text-align: center;
      
    }
  }